.content {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 100%;
  min-width: 320px;
  max-width: 1300px;
  width: 100%;
  padding: 0 10px;
  flex-wrap: wrap;

  .link {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 40px;
    height: 100%;
    text-decoration: none;
    color: #2b2929;
    font-weight: bold;
    transition: 1s ease;

    .countBlock {
      position: relative;

      .countProducts {
        position: absolute;
        top: -9px;
        left: 57px;
        width: 25px;
        height: 25px;
        border-radius: 50%;
        font-size: 12px;
        line-height: 13px;
        font-weight: 500;
        border: 1px solid;
        display: flex;
        justify-content: center;
        align-items: center;
      }
    }

    &:hover {
      transform: scale(1.3);
      transition: 1s ease;
    }

    .logo {
      height: 90px;
      padding: 10px;
    }
  }

  .login {
    display: flex;
    justify-content: space-evenly;

    .icon {
      width: 35px;
      height: 35px;
      margin-right: 15px;
    }
  }
}



